@font-face {
  font-family: 'MyWebFont';
  src: url('../../fonts/VESPASIAN_TIMES.ttf')
  format('truetype');
}

:root {
  max-width: 640px;
  margin: 0 auto 0 auto;
  --home-logo-height: 50vh;
  --home-logo-margin: 9vh;
}

section.home {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
}

.logoWrapper {
    margin-top: var(--home-logo-margin);
    height: var(--home-logo-height);
}

.staticLogo {
  margin-top: var(--home-logo-margin);
  height: var(--home-logo-height);
}

.logoAlign {
  position: absolute;
  height: var(--home-logo-height);
}

.logo {
  opacity: 0;
  animation: logoIntro 4.27s linear, fadeIn 1s linear 3.27s forwards;
}

.lightLogo {
  animation: logoIntro 4.27s linear, fadeOut 1s linear 3.27s forwards;
}

.logoTextAnimation {
  margin-top: 4px;
  animation: fadeIn linear 1s forwards;
  animation-delay: 3.27s;
  opacity: 0;
}

.logoTextStyles {
  font-family: MyWebFont;
  color: gold;
  font-size: 12vh;
  text-transform: lowercase;
  line-height: .5;
}

@keyframes fadeOut {
  0% {
    opacity:1;
  }
  100% {
    opacity:0;
  }
}

@keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}

@keyframes logoIntro {
  0% {
    transform: scale(0) rotate(0deg);
  }
  33% {
    transform: scale(.363) rotate(2880deg);
  }
  66% {
    transform: scale(.678) rotate(3960deg);
  }
  100% {
    transform: scale(1) rotate(4320deg);
  }
}
